/* reset */
$min-width: 320px; 
$max-width: 1250px;
$base-bg: #FFF; 
$base-font-size: 16px; 
$base-line-height: 26px; 
$base-font-family: 'Roboto', sans-serif; 
$custom-font-family: 'Roboto', sans-serif; 
$link-color: #100da9;
$transition: all 0.1s linear;

/* main variables */

//text, titles
$text-color: #212121;
$title-color: #212121;
$title-font-family: $custom-font-family;

$font-size-h1: 36px;
$line-height-h1: 42px;
$font-size-h2: 32px;
$line-height-h2: 37px;
$font-size-h3: 28px;
$line-height-h3: 32px;